a.nav-link {
  color: #fff;
  font-size: 0.8em;
  text-transform: uppercase;
  transition: 0.4s ease-in-out;
  position: relative;
  border-bottom: 2px solid transparent;
  margin-right: 1em;
  @include breakpoint (xs) {
  	font-size: 1em;
  	margin-left: 15px;
  	margin-bottom: 15px;
  	color: #000;
  	width: 45%;
  }
  @include breakpoint (sm) {
  	font-size: 0.71em;
  }
  @include breakpoint (md) {
  	font-size: 1em;
  }
  &:hover {
  	color: #81db6d;
  	transition: 0.4s ease-in-out;
  }
}
.navbar-toggler {
    background-color: #81db6d;
    border: 0px solid transparent;
    transition: 0.4s ease-in-out;
    &:hover {
    	background-color: #8ef577;
    }
    &:focus {
    	outline: none;
    }
}
.navbar-collapse.collapsing,
.navbar-collapse.show {
    background: #f8f9fa;
    margin-left: -15px;
    margin-right: -15px;
    @include breakpoint (sm) {
    	
    }
}
